.StaffMember {
  width: calc(50% - 4rem);
  margin: 1rem 0;
}

.StaffMember--Title {
  font-size: 3.5rem;
  margin-top: 2rem;
}

.StaffMember--Subtitle {
  font-weight: 100;
  font-size: 1.2rem;
  margin: 0 0 2rem 0;
}

.StaffMember--Image {
  width: 100%;
  height: auto;
}

.StaffMember--Image .LazyImage {
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
  -o-object-fit: contain;
     object-fit: contain;
  opacity: 0.8;
  mix-blend-mode: lighten;
}

@supports ((-o-object-fit: contain) or (object-fit: contain)) {
  .StaffMember--Image .LazyImage {
    width: 100%;
    height: 100%;
    -o-object-fit: contain;
       object-fit: contain;
  }
}

.StaffMember--ContentWrap {
  width: 100%;
  max-width: 400px;
}



