.WelcomeAnimation {
  height: 50px;
  width: 100%;
  overflow: hidden;
  position: fixed;
  top: 23vh;
  left: 0;
  z-index: 5;
}

body.fixed {
  overflow: hidden;
  /* height: 0; */
}

.WelcomeAnimation--Studio {
  display: block;
  height: 50px;
  width: auto;
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translate(6px, 0);
      -ms-transform: translate(6px, 0);
          transform: translate(6px, 0);
}

.WelcomeAnimation--Spacial {
  display: block;
  height: 50px;
  width: auto;
  position: absolute;
  top: 0;
  right: 50%;
  -webkit-transform: translate(-6px, 0);
      -ms-transform: translate(-6px, 0);
          transform: translate(-6px, 0);
}

body.fixed .WelcomeAnimation {
  height: 100vh;
  width: 100%;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  background: var(--primary);
  z-index: 5;
  -webkit-animation: pulse 3s forwards;
          animation: pulse 3s forwards;
}
body.fixed .WelcomeAnimation--Studio {
  display: block;
  height: 50px;
  width: auto;
  position: absolute;
  top: 25vh;
  left: 50%;
  -webkit-transform: translate3d(6px, -50%, 0);
          transform: translate3d(6px, -50%, 0);
  -webkit-animation: slideLeft 2s forwards;
          animation: slideLeft 2s forwards;
}

body.fixed .WelcomeAnimation--Spacial {
  display: block;
  height: 50px;
  width: auto;
  position: absolute;
  top: 25vh;
  right: 50%;
  -webkit-transform: translate3d(-6px, -50%, 0);
          transform: translate3d(-6px, -50%, 0);
  -webkit-animation: slideRight 2s forwards;
          animation: slideRight 2s forwards;
}
body.scrolled .WelcomeAnimation--Spacial,
body.scrolled .WelcomeAnimation--Studio {
  display: none;
}

@-webkit-keyframes pulse {
  50% {
    background-color: var(--primary);
  }
  100% {
    background-color: transparent;
  }
}

@keyframes pulse {
  50% {
    background-color: var(--primary);
  }
  100% {
    background-color: transparent;
  }
}
@-webkit-keyframes slideRight {
  0% {
    -webkit-transform: translate3d(-9999px, -50%, 0);
            transform: translate3d(-9999px, -50%, 0);
  }
  100% {
    -webkit-transform: translate3d(-6px, -50%, 0);
            transform: translate3d(-6px, -50%, 0);
  }
}
@keyframes slideRight {
  0% {
    -webkit-transform: translate3d(-9999px, -50%, 0);
            transform: translate3d(-9999px, -50%, 0);
  }
  100% {
    -webkit-transform: translate3d(-6px, -50%, 0);
            transform: translate3d(-6px, -50%, 0);
  }
}
@-webkit-keyframes slideLeft {
  0% {
    -webkit-transform: translate3d(9999px, -50%, 0);
            transform: translate3d(9999px, -50%, 0);
  }
  100% {
    -webkit-transform: translate3d(6px, -50%, 0);
            transform: translate3d(6px, -50%, 0);
  }
}
@keyframes slideLeft {
  0% {
    -webkit-transform: translate3d(9999px, -50%, 0);
            transform: translate3d(9999px, -50%, 0);
  }
  100% {
    -webkit-transform: translate3d(6px, -50%, 0);
            transform: translate3d(6px, -50%, 0);
  }
}



