.TestimonialCard {
  text-decoration: none;
  opacity: 0.3;
  padding: 1rem 6rem;
  width: 100%;
  max-width: 500px;
  -webkit-transition: opacity 0.2s ease-out;
  -o-transition: opacity 0.2s ease-out;
  transition: opacity 0.2s ease-out;
}
.TestimonialCard.is-selected {
  opacity: 1;
  border-left: 1px solid var(--midGrey);
  border-right: 1px solid var(--midGrey);
}

.TestimonialCard .TestimonialCard--Image {
  margin: 0 0 2rem 0;
}
.TestimonialCard h3 {
  margin: 0 0 0 4rem;
}
.TestimonialCard .button {
  margin: 2rem 6rem;
}



