.Logo {
  display: block;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  height: 15rem;
  width: 15rem;
  margin-right: 1rem;
  color: currentColor;
}

body.scrolled .Logo {
  -webkit-transform: scale(0.5);
      -ms-transform: scale(0.5);
          transform: scale(0.5);
  position: absolute;
  top: -3.5rem;
  left: 0;
}



